@media (max-width: 320px) { .cabinet__title { font-size: 16px; } }

@media (min-width: 1199px) { .cabinet__title { font-size: 24px; } }

@media only screen and (max-width: 1399px) { .cabinet__title { padding-left: 15px; padding-right: 15px; } }

@media only screen and (max-width: 767px) { .cabinet__title { padding-right: 0; display: inline-block; vertical-align: middle; width: calc(100% - 65px); } }

@media only screen and (max-width: 767px) { .cabinet__btn { margin-top: 15px; } }

.cabinet__blocks { display: flex; justify-content: flex-start; align-items: normal; flex-direction: row; flex-wrap: wrap; margin-left: -15px; margin-right: -15px; }

.cabinet__block { width: 100%; margin-bottom: 30px; padding-left: 15px; padding-right: 15px; }

.cabinet__block-2-3 { width: 66.66667%; }

@media only screen and (max-width: 991px) { .cabinet__block-2-3 { width: 100%; } }

.cabinet__block-1-3 { width: 33.33333%; }

@media only screen and (max-width: 991px) { .cabinet__block-1-3 { width: 100%; } }

.cabinet-block { background-color: #fff; border-radius: 30px; box-shadow: 0px 5px 30px 0px rgba(0, 0, 0, 0.08); padding: 30px 40px; }

@media only screen and (max-width: 1199px) { .cabinet-block { padding: 30px; } }

@media only screen and (max-width: 991px) { .cabinet-block { padding: 25px; } }

@media only screen and (max-width: 767px) { .cabinet-block { padding: 15px; border-radius: 15px; } }

.cabinet-block__title { color: #7d7d7d; font-family: 'montserrat-bold', Arial, sans-serif; font-size: 18px; }

@media only screen and (max-width: 767px) { .cabinet-block__title { font-size: 16px; } }

.cabinet-table { color: #535353; }

.cabinet-table table th { font-weight: 400; font-family: 'montserrat-bold', Arial, sans-serif; font-size: 18px; color: #7d7d7d; text-align: left; }

.cabinet-table table th:nth-shild(1) { width: 70%; }

.cabinet-table table th:nth-shild(2) { width: 30%; }

@media only screen and (max-width: 767px) { .cabinet-table table th { font-size: 16px; } }

.cabinet-table table tr:not(:last-child) { border-bottom: 1px solid #dedede; }

.cabinet-table table tr:last-child td { padding-bottom: 0; }

.cabinet-table table td, .cabinet-table table th { padding: 20px 10px; }

@media only screen and (max-width: 1199px) { .cabinet-table table td, .cabinet-table table th { padding: 15px 10px; } }

@media only screen and (max-width: 991px) { .cabinet-table table td, .cabinet-table table th { padding: 10px; } }

@media only screen and (max-width: 767px) { .cabinet-table table td, .cabinet-table table th { padding: 5px 10px; } }
