body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  @include reg;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm; } }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit; } }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.logo {
  display: block;
  @include sm {
    width: 60px; } }

.green {
  color: $green; }

.text-center {
  text-align: center; }

.btn {
  text-transform: uppercase;
  color: $white;
  background-color: $accent;
  border: 2px solid  $accent;
  display: inline-block;
  min-width: 260px;
  padding: 11px 10px;
  text-align: center;
  @include bold;
  border-radius: 25px;
  @include tr(background-color .3s, color .3s, border-color .3s );
  @include hover {
    color: $accent;
    background-color: transparent; }

  &--transp-white {
    background-color: transparent;
    color: $white;
    border-color: $white;
    @include hover {
      color: $accent;
      background-color: $white; } }
  &--sm {
    font-size: $fz;
    min-width: 170px;
    padding: 14px 10px 12px; }
  &--reg {
    @include reg; }
  &--arrow {
    position: relative;
    @include inline-flex(space-between, center);
    padding-left: 30px;
    padding-right: 30px;
    @include after {
      @include icon-arrow(12, 12, 1, currentColor, -135);
      display: inline-block; } }
  &--rect {
    border-radius: 0; } }

.quest {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  text-align: center;
  background-color: $gray-light;
  font-size: .5em;
  color: $white;
  position: relative;
  top: -.35em; }

.number {
  color: $blue;
  @include css-lock(48, 120, 320, 1199, font-size);
  line-height: 1.2;
  @include bold; }


